.productList{
display: grid;
grid-template-columns: 5fr 5fr 1fr;
grid-template-rows: 1fr 1fr 1fr;
gap: 20px 20px;


  &::after{
    display: flexbox;
    flex-wrap: wrap;
    content: "";
    clear: both;
  }
  .card{
    background-color: whitesmoke;
    padding: 20px;
  }

  .imageHolder{
    display: flex;
    flex-wrap: wrap;
    background-size: cover;
    justify-content: space-evenly;
    text-align: center;
    position: relative;
    >img{
      width: auto;
      max-height: 300px;
      object-fit: contain;
    }
    button{
     
      margin-right: 300px;
      padding: 10px  15px;
      color: white;
      background-color:black;
      border: none;
     font-family: sans-serif;
      cursor: pointer;
    }
  }
}